import React from "react"
import {rhythm} from "../utils/typography"
import presets from "../utils/presets"

const BusinessCard = (props) =>(
  <div css={{
    display: 'flex',
    flexDirection: 'row',
    margin: '0.5rem 0rem',
    padding: rhythm(1),
    boxShadow: `0em 0em 1em ${presets.brandColor}`,
  }} >
    <div style={{
      display: 'flex',
      flex: 1,
      maxHeight: `100px`,
      color: presets.brandColor
    }} >
      {props.children}
    </div>
    <div style={{
      flex: 2
    }} >
      <div>
        {props.Location}
      </div>
      <div>
        {props.Address}
      </div>
      <div>
        {props.Contact}
      </div>
      {props.Directions==undefined ||
        <a href={props.Directions}>Directions</a>
      }
      </div>
  </div>
)

export default BusinessCard;



