import React from "react"
import BusinessCard from "../components/businesscard"
import MdPhone from "react-icons/lib/md/phone"
import MdEmail from "react-icons/lib/md/email"

const ContactPage = () =>(
  <div>
    <h2>Contact Us.</h2>
    <p>Get in touch with us to discuss our products.</p>
    <BusinessCard
      Location="Call us on"
      Address="Ph: +917355105451"
      Contact="Ph: +917985269507"
    >
      <MdPhone height="auto" width="100%"/>
    </BusinessCard>
    <BusinessCard
      Location="Email"
      Address="venovelhealthcare@gmail.com"
    >
      <MdEmail height="auto" width="100%"/>
    </BusinessCard>
  </div>
)

export default ContactPage



